export default {
  namespaced: true,
  state: {
    user:null
  },
  mutations: {
    SET_USER: function (state, payload) {
      state.user = {...payload}
    }
  },
  getters: {
    getUserData: (state) => {
      return { ...state.user }
    },
    ranked: (state) => {
      return { elo: state.user.elo, id: state.user.id }
    },
  },
  actions: {
  },
}